.contact-us {
    text-align: center;
    padding: 2rem;
  }
  h2 { font-weight: normal; }

  
  .icons {
    margin: 1.5rem 0;
  }
  
  .icons a {
    margin: 0 1rem;
    color: inherit;
    text-decoration: none;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 500px;
    margin: 0 auto;
  }
  
  .contact-form label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 1rem;
    font-size: 1rem;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 0.5rem;
    margin-top: 0.5rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .contact-form button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background-color: #000000;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 1rem;
  }
  
  .contact-form button:hover {
    background-color: #707070;
  }
  